@charset "UTF-8";
.fpt-sub-banner {
  position: relative;
}
.fpt-sub-banner .slogan {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-50%, 50%);
}
@media (max-width: 576px) {
  .fpt-sub-banner .slogan {
    max-width: 12rem;
  }
  .fpt-sub-banner .slogan img {
    max-height: 1rem;
  }
}
.fpt-sub-banner .breadcrumb-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}
.fpt-sub-banner .breadcrumb-wrapper .container {
  background: rgba(242, 111, 33, 0.5);
}
.fpt-sub-banner .breadcrumb-wrapper ol {
  padding: 8px 0 8px 8px;
  list-style: none;
  display: flex;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li {
  margin-left: 16px;
  padding-left: 16px;
  position: relative;
}
.fpt-sub-banner .breadcrumb-wrapper ol li + li:before {
  position: absolute;
  left: -8px;
  top: 55%;
  transform: translateY(-50%);
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f142";
  color: #fff;
  font-size: 18px;
}
.fpt-sub-banner .breadcrumb-wrapper ol li a {
  color: #fff;
  text-decoration: none;
  white-space: nowrap;
  font-family: "Muli";
  font-size: 15px;
}
@media (max-width: 768px) {
  .fpt-sub-banner .breadcrumb-wrapper {
    display: none;
  }
}

.fpt-register .container {
  background: #fff;
}
.fpt-register .section-wrapper {
  padding: 72px 0 60px;
}
.fpt-register .alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}
.fpt-register .alert-success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}
.fpt-register .alert-success hr {
  border-top-color: #b1dfbb;
}
.fpt-register .alert-success .close {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0.75rem 1.25rem;
  color: inherit;
}
.fpt-register button.close {
  padding: 0;
  background-color: transparent;
  border: 0;
  appearance: none;
  display: none;
}
.fpt-register h1, .fpt-register h2, .fpt-register h3 {
  text-transform: uppercase;
  text-align: center;
  font-size: 35px;
  margin-bottom: 9px;
  font-family: "Roboto Slab";
}
.fpt-register .col-lg-6 {
  max-width: 100%;
}
.fpt-register .register-form-wrapper {
  margin: 0 auto;
  font-family: "Muli";
}
.fpt-register .form-group {
  margin-bottom: 26px;
}
.fpt-register .form-group label {
  display: block;
  color: #666666;
  font-size: 14px;
  margin-bottom: 10px;
}
.fpt-register .form-group input[type=text], .fpt-register .form-group select {
  width: 100%;
  height: 40px;
  background: #f5f5f5;
  border: 1px solid #ebebeb;
  padding-left: 14px;
  color: #f26f21;
  border-radius: 5px;
}
.fpt-register .form-group input::placeholder {
  color: #999999;
}
.fpt-register .form-group select {
  color: #999999;
}
.fpt-register .form-group .RadPicker {
  position: relative;
  width: 100% !important;
}
.fpt-register .form-group .RadPicker .rcCalPopup {
  position: absolute;
  transform: translateY(-50%);
  right: 5px;
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus {
  width: 100%;
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus tr {
  display: flex;
}
@media (max-width: 768px) {
  .fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus tr {
    flex-wrap: wrap;
  }
  .fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus tr td {
    flex: 0 0 50% !important;
  }
}
@media (max-width: 350px) {
  .fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus tr td {
    flex: 0 0 100% !important;
  }
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus td {
  display: flex;
  flex: auto;
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus td input {
  display: none;
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus td label {
  position: relative;
  padding-left: 26px;
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus td label:before {
  content: "";
  position: absolute;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  border: 1px solid #999999;
  background: #fff;
  margin-right: 13px;
  left: 0;
  top: 50%;
  transform: translateY(-60%);
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus td label:after {
  content: "";
  position: absolute;
  width: 6.5px;
  height: 6.5px;
  border-radius: 50%;
  background: #f26f21;
  margin-right: 13px;
  left: 0;
  top: 50%;
  transform: translate(50%, -65%);
  display: none;
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus td input:checked ~ label:before {
  border: 1px solid #f26f21;
}
.fpt-register .form-group table#ctl00_mainContent_ctl01_rblCampus td input:checked ~ label:after {
  display: block;
}
.fpt-register .frm-btnwrap {
  width: 271px;
  margin: 0 auto;
}
.fpt-register .frm-btnwrap .frm-btn {
  position: relative;
}
.fpt-register .frm-btnwrap .frm-btn:before {
  position: absolute;
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\f35f";
  color: #fff;
  font-size: 30px;
  top: 50%;
  right: 70px;
  transform: translateY(-50%);
}
.fpt-register .frm-btnwrap .frm-btn-reset {
  display: none;
}
.fpt-register .frm-btnwrap input {
  width: 100%;
  height: 50px;
  border: 0;
  background: #fcb338;
  font-family: "Roboto Slab";
  color: #fff;
  text-transform: uppercase;
}
.fpt-register .fa-exclamation-triangle {
  color: #ff0000;
  margin-top: 6px;
}