	.frm-btnwrap
		width: 271px
		margin: 0 auto
		.frm-btn
			position: relative
			&:before
				position: absolute
				+mtrl('\f35f')
				color: #fff
				font-size: 30px
				top: 50%
				right: 70px
				transform: translateY(-50%)
		.frm-btn-reset
			display: none
		input
			width: 100%
			height: 50px
			border: 0
			background: #fcb338
			font-family: 'Roboto Slab'
			color: #fff
			text-transform: uppercase
	.fa-exclamation-triangle
